import { useState } from 'react';
import Modal_08 from '../components/Modal_08';
import Backdrop_08 from '../components/Backdrop_08';

function  Todo_08(props) {
    const [showModal, setShowModal] = useState();

    function showModalHandler(){
      setShowModal(true)
    }

    function closeModalHandler(){
      setShowModal(false);
    }
    return (
      <div>
        <div className="card">
          <h2>{props.text}</h2>
          <div className="actions">
            <button className="btn" onClick={showModalHandler}>Delete</button>
          </div>
        </div>
        {showModal && <Backdrop_08 onClose={closeModalHandler}/>}
        {showModal && <Modal_08 text='Are you sure?' onClose={closeModalHandler}/>}
      </div>
      
   );
}

export default Todo_08;